html {
    background-image: url("../images/about3.jpg");
}

a {
    text-decoration: none;
}

.section {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.section-row {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.section-row-only {
    position: relative;
    display: flex;
    flex-flow: row wrap;
}

.wrap-row {
    flex-flow: row wrap;
}

.section-static {
    position: relative;
    min-height: 50vh;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.parallax::after {
    content: " ";
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;

    transform: translateZ(-1px) scale(1.5);
    background-size: cover;
    z-index: -50;
}

.bg1::after {
    background-image: url('../images/home.jpg');
    position: absolute;
    left: -5px;
}

.bg2::after {
    background-image: url('../images/about3.jpg');
    background-position: center;
    background-attachment: fixed;
    transform: none;
}

.static {
    background-color: rgba(255, 255, 255, 1);
}

.header {
    font-family: "Berlin Sans FB";
    font-size: calc(20px + 1.5vw);
    font-weight: lighter;
    color: white;
}

.name {
    margin-top: 20px;
    font-family: "Berlin Sans FB";
    font-size: calc(50px + 3.5vw);
    font-weight: bolder;
    color: white;
}