.black-text {
    color: black;
}

.white-text {
    color: white;
}

.light-font {
    font-weight: 300;
}

.container-wrapper {
    display: flex;
    flex-flow: row wrap;
    padding: 15px;
}

.no-padding {
    padding: 0px;
}

.container-wrapper-2 {
    display: flex;
    flex-flow: row;
    padding: 15px;
}

.portfolio-container {
    display: flex;
    flex-flow: column;
    background-color: rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    width: 40vw;
    height: 80vh;
}

.portfolio-container-2 {
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    width: calc((90vw - 200px) / 4);
    min-width: 200px;
    min-height: calc((200px - 20px) * 0.6);
}

.skills-container {
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    width: 40vw;
    height: 80vh;
    margin-right: 20px;
}

.skills-container-2 {
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    width: auto;
}

.scrollable {
    overflow-y: auto;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.scrollable::-webkit-scrollbar {
    background-color: rgba(0, 0, 0, 0);
}

.scrollable::-webkit-scrollbar-thumb {
    background-color: orange;
}

.box-shadow {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.box-shadow-2 {
    box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.22), 0 7px 21px 0 rgba(0, 0, 0, 0.21);
}
.box-shadow-blue {
    box-shadow: 0 4px 8px 0 rgb(0, 183, 255, 0.3), 0 6px 20px 0 rgb(0, 183, 255, 0.29);
}

.auto-height-no {
    height: auto !important;
}

.no-background {
    background-color: rgba(255, 255, 255, 0);
}

.white-background {
    background-color: white;
}

.no-border {
    border: none;
}

.make-circle {
    margin-left: 20px;
    margin-right: 20px;
    display: inline block;
}

.orange-circle {
    width: 15px;
    height: 15px;
    border: 0.5px solid orange;
    border-radius: 50%;
    background-color: orange;
}

.blue-circle {
    width: 15px;
    height: 15px;
    border: 0.5px solid #00b7ff;
    border-radius: 50%;
    background-color: #00b7ff;
}

.white-circle {
    width: 15px;
    height: 15px;
    border: 0.5px solid #00b7ff;
    border-radius: 50%;
    background-color: white;
}

.white-box {
    margin: 0;
    padding: 10px;
    width: auto;
    min-width: 100px;
    max-width: 20vw;
    border-radius: 20px;
    background-color: white;
}

.margin-top {
    margin-top: 50px !important;
}

.margin-top-- {
    margin-top: 20px !important;
}

.margin-top-2 {
    margin-top: 100px !important;
}

.margin-bottom-2 {
    margin-bottom: 60px !important;
}

.margin-bottom {
    margin-bottom: 50px !important;
}

.margin-right {
    margin-right: 20px !important;
}

.margin-right-- {
    margin-right: 5px !important;
}

.margin-left {
    margin-left: 20px !important;
}

.margin-left-- {
    margin-left: 5px !important;
}


.margin-left-2 {
    margin-left: 40px !important;
}

.no-margin {
    margin: 0;
}

.no-padding {
    padding: 0;
}

.portfolio-image {
    height: calc((90vw - 200px) * 0.6 / 4);
    width: calc((90vw - 200px) / 4);
    min-width: 200px;
    min-height: calc(200px * 0.6);
}

.box-hover {
    transition: 0.15s ease-in;
}

.box-hover:hover {
    box-shadow: 0 15px 19px 0 rgba(0, 0, 0, 0.24), 0 17px 31px 0 rgba(0, 0, 0, 0.23);
}

@media only screen and (max-width: 600px) {
    .portfolio-container-2 {
        width: calc(90vw - 100px) !important;
        min-height: calc((90vw - 100px) * 0.6) !important;
        margin: 10px !important;
    }

    .portfolio-image {
        width: calc(90vw - 100px) !important;
        min-height: calc((90vw - 80px) * 0.6) !important;
    }

    .skills-container {
        width: 90vw !important;
        margin-right: 0px;
        margin-bottom: 20px;
    }

    .portfolio-container {
        width: 90vw !important;
    }

    .scrollable::-webkit-scrollbar {
        display: block;
    }
}